var targetManager = require('targetManager');
const { UIHelp } = require('../UIHelp');
cc.Class({
    extends: cc.Component,

    properties: {
        //
        pronum_label: [cc.Label],
        //
        propnode: [cc.Node],
        //
        music: {
            type: cc.AudioClip,
            default: [],
        },
        //
        shop: null,
        ShopPrefab: cc.Prefab,
    },
    onLoad() {
        cc.ZL.PropMgr = this;
        this.showProp();
        this.num = 0;
    },
    update(dt) {
        this.showProp();
    },
    //
    showProp() {
        for (var i = 0; i < this.pronum_label.length; i++) {
            if (i == 5) {
                this.pronum_label[5].string = gamedata.prop[4];
            } else {
                this.pronum_label[i].string = gamedata.prop[i + 1];
            }
        }
    },
    //
    PropBtn(target, event) {
        cc.ZL.musicMgr.MyplayMusic(0, false);
        event = parseInt(event);
        for (var i = 0; i < this.propnode.length; i++) {
            this.propnode[i].stopAllActions();
            this.propnode[i].scale = 0.7;
        }
        if (gamedata.prop[event] <= 0) {
            if (this.shop == null) {
                this.shop = cc.instantiate(this.ShopPrefab);
                this.node.parent.addChild(this.shop);
                //this.shop.setPosition(0,1000);
            }
            this.shop.getComponent('Shop').Open();
            return;
        }
        //this.playMusic(0);
        if (gamedata.is_prop == parseInt(event)) {
            gamedata.is_prop = 0;
            target.currentTarget.scale = 0.7;
            target.currentTarget.stopAllActions();
        } else {
            if (parseInt(event) == 4) {
                this.AddStep();
                return;
            } else if (parseInt(event) == 3) {
                this.AnimHuan();
                // cc.ZL.animMgr.replaceAll();
                // this.changeData(3);
                // gamedata.is_prop = 0;
                return;
            }
            gamedata.is_prop = parseInt(event);
            this.selecteffect(target.currentTarget);
        }
    },
    AnimHuan() {
        // cc.ZL.animMgr.replaceAll();
        // this.changeData(3);
        // gamedata.is_prop = 0;

        let preas = {
            rid: gamedata.rid,
            token: gamedata.token,
            gid: 10002,
            num: 1,
        };
        // TODO:
        // UIHelp.network.httpConnect_post_async('game/xiaoxiaole/consumeProp', preas, this.AnimHuanCallBack.bind(this));
        this.AnimHuanCallBack({
            code: '200',
            message: '',
        });
    },

    AnimHuanCallBack(msg) {
        console.log('AnimHuanCallBack===>', msg);
        if (msg.code == 200) {
            cc.ZL.animMgr.replaceAll();
            this.changeData(3);
            gamedata.is_prop = 0;
        } else {
            utils.addTips(msg.message);
        }
    },
    //
    AddStep() {
        let preas = {
            rid: gamedata.rid,
            token: gamedata.token,
            gid: 10003,
            num: 1,
        };
        // TODO:
        UIHelp.network.httpConnect_post_async('game/xiaoxiaole/consumeProp', preas, this.AddStepCallBack.bind(this));
        // gamedata.is_prop = 0;
        // cc.ZL.UIMgr.AddFiveStep();
        // this.changeData(6);
    },

    AddStepCallBack(msg) {
        console.log('AddStepCallBack===>', msg);
        if (msg.code == 200) {
            gamedata.is_prop = 0;
            cc.ZL.UIMgr.AddFiveStep();
            this.changeData(6);
        } else {
            utils.addTips(msg.message);
        }
    },
    //
    selecteffect(node) {
        var e1 = cc.scaleTo(0.5, 0.8, 0.8);
        var e2 = cc.scaleTo(0.5, 0.6, 0.6);
        var seq = cc.sequence([e1, e2]);
        var rep = cc.repeatForever(seq);
        node.runAction(rep);
    },
    //
    propFinished(num) {
        gamedata.is_prop = 0;
        //this.playMusic(0);
        for (var i = 0; i < this.propnode.length; i++) {
            this.propnode[i].stopAllActions();
            this.propnode[i].scale = 0.7;
        }
        this.changeData(num);
    },
    //
    changeData(num) {
        num = parseInt(num);
        this.num = num;
        if (num == 6) {
            gamedata.prop[4] -= 1;
        } else {
            gamedata.prop[num] -= 1;
        }
        this.pronum_label[num - 1].string = gamedata.prop[num];
        gamedata.bc_prop(num);
        // let gid;
        // if (num == 1) {
        //     gid = 10001
        // } else if (num == 3) {
        //     gid = 10002
        // } else if (num == 6) {
        //     gid = 10003
        // }
    },
    //
    playMusic(num) {
        if (gamedata.music) {
            cc.audioEngine.play(this.music[num], false, 1);
        }
    },
});
