const { UIHelp } = require('../UIHelp');

cc.Class({
    extends: cc.Component,

    properties: {
        music: {
            type: cc.AudioClip,
            default: null,
        },
        //
        tipLabel: [cc.Label],
        //
        pro_loading: cc.ProgressBar,

        //
        Logo: cc.Node,
    },

    onLoad() {
        UIHelp.init();
        if ((cc.ZL = null)) {
            cc.ZL = {};
        }
        //wx.clearStorage();
        // gamedata.GetData();
        cc.audioEngine.play(this.music, true, 0.3);
        this.LogoAnim();

        // this.LoadingChild();
    },

    start() {},
    //
    LogoAnim() {
        let anim = this.Logo.getComponent(sp.Skeleton);
        anim.clearTracks();
        anim.addAnimation(0, 'start', false, 0);
        anim.addAnimation(0, 'play', false, 0);
        anim.addAnimation(0, 'end', false, 0);
        anim.setEndListener(
            function (res) {
                if (res.animation.name == 'play') {
                    this.LoadingChild();
                }
            }.bind(this)
        );
    },
    //
    LoadingChild() {
        let self = this;
        try {
            const data = {
                roomID: '1',
                token: '2',
            };

            if (data.roomID) {
                gamedata.rid = data.roomID;
            }
            if (data.token) {
                gamedata.token = data.token;
            }
            cc.director.loadScene('newLevel');
            if (!gamedata.rid || !gamedata.token) {
                utils.addTips('');
                return;
            }

            // winOcx.getUserData((data) => {
            //     if (data.roomID) {
            //         gamedata.rid = data.roomID;
            //     }
            //     if (data.token) {
            //         gamedata.token = data.token;
            //     }
            //     if (!gamedata.rid || !gamedata.token) {
            //         utils.addTips('');
            //         return;
            //     }
            // });
        } catch (error) {
            gamedata.rid = utils.getQueryString('rid');
            gamedata.token = utils.getQueryString('token');
            if (!gamedata.rid || !gamedata.token) {
                utils.addTips('');
                return;
            }
            this.login();
        }
    },
    login() {
        let preas = {
            rid: gamedata.rid,
            token: gamedata.token,
        };
        UIHelp.network.httpSend('game/xiaoxiaole/getGameInfo', preas, this.loginCall.bind(this));
    },
    loginCall(msg) {
        console.log('login==>', msg);
        if (msg.code == 200) {
            gamedata.Lv_star = Number(msg.data.current_level);
            // gamedata.Lv_star = 74;
            gamedata.level_stars = msg.data.level_stars;
            //
            gamedata.gold = Number(msg.data.coin);
            //
            gamedata.energy = Number(msg.data.energy);
            let preas = {
                rid: gamedata.rid,
                token: gamedata.token,
            };
            UIHelp.network.httpConnect_post_async(
                'game/xiaoxiaole/getMyPropList',
                preas,
                this.getMyPropListCallBack.bind(this)
            );
        } else {
            utils.addTips(msg.message);
        }
    },

    getMyPropListCallBack(msg) {
        console.log('getmyPrropList====>', msg);
        if (msg.code == 200) {
            if (msg.data.length > 0) {
                for (let a = 0; a < msg.data.length; a++) {
                    if (msg.data[a].gid == 10001) {
                        gamedata.prop[1] = Number(msg.data[a].num);
                    } else if (msg.data[a].gid == 10002) {
                        gamedata.prop[3] = Number(msg.data[a].num);
                    } else if (msg.data[a].gid == 10003) {
                        gamedata.prop[4] = Number(msg.data[a].num);
                    }
                }
            }
            this.preLoading();
        } else {
            utils.addTips(msg.message);
        }
    },
    //
    preLoading() {
        let self = this;
        // self.tipLabel[1].string = "......";
        self.is_Loading = true;
        let onProcess = function (completecount, totalcount, item) {
            if (self.is_Loading) {
                let progress = (completecount / totalcount) * 0.5;
                self.tipLabel[0].string = Math.floor(progress * 100) + '%';
                //console.log(progress);
                self.pro_loading.progress = progress;
            }
        };
        let onProcess1 = function (completecount, totalcount, item) {
            if (self.is_Loading) {
                let progress = (completecount / totalcount) * 0.5 + 0.5;
                self.tipLabel[0].string = Math.floor(progress * 100) + '%';
                //console.log(progress);
                self.pro_loading.progress = progress;
            }
        };
        cc.director.preloadScene('newLevel', onProcess, () => {
            cc.director.preloadScene('NewMain', onProcess1, () => {
                self.is_Loading = false;
                setTimeout(
                    function () {
                        cc.director.loadScene('newLevel');
                    }.bind(this),
                    1000
                );
            });
        });
        // cc.loader.loadResDir("texture", onProcess, function (err, assets) {
        //     if (err) {
        //         self.tipLabel[1].string = "";
        //     } else {
        //         self.is_Loading = false;
        //         setTimeout(function () {
        //             cc.director.loadScene("newLevel");
        //         }.bind(this), 1000);
        //         // self.tipLabel[1].string = ".......";
        //         // cc.loader.loadResDir('levels', cc.TextAsset, (err, res) => {
        //         //     if (err) {
        //         //         console.log("load res error=" + err);
        //         //     } else {
        //         //         // gamedata.Lv_star = res.length - 1;
        //         //         // console.log('sdafsdfa', gamedata.Lv_star)
        //         //         self.tipLabel[1].string = "";
        //         //         setTimeout(function () {
        //         //             cc.director.loadScene("newLevel");
        //         //         }.bind(this), 1000);
        //         //     }
        //         // })

        //     }
        // })
    },
});
