
cc.Class({
    extends: cc.Component,

    properties: {
        //
        lvLabel: cc.Label,
        stepnum: -1,
        //
        stepLabel: cc.Label,
        //
        _score: 0,
        //
        socreProcess: cc.ProgressBar,
        //
        scoreLabel: cc.Label,
        //Logo
        logo: cc.Node,

        add_score: 150,

        //
        end: cc.Prefab,

        xing1: cc.Node,
        xing2: cc.Node,
        xing3: cc.Node
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
         cc.ZL.animMgr.mask.active = true;
        cc.ZL.UIMgr = this;
        this.showInfo();
    },

    start() {

    },

    // update (dt) {},
    showInfo() {
        this.lvLabel.string = '' + comeInfo.now_lv + '';
        // this.stepnum=lvdata["Lv"+comeInfo.now_lv].step;
        this.stepnum = lvdata.step;
        this.stepLabel.string = this.stepnum;
        this.AddScore(0);
    },
    //
    AddScore(num) {
        this._score += num;
        this.scoreLabel.string = this._score;
        // let bar=this._score/(lvdata["Lv"+comeInfo.now_lv].one_star*3);
        let bar = this._score / (lvdata.one_star * 3);
        this.socreProcess.progress = bar;
        let sum = this.GetStarNum();
        switch (sum) {
            case 3:
                this.xing3.active = true;
                this.xing2.active = true;
                this.xing1.active = true;
                break;
            case 2:
                this.xing3.active = false;
                this.xing2.active = true;
                this.xing1.active = true;
                break;

            case 1:
                this.xing3.active = false;
                this.xing2.active = false;
                this.xing1.active = true;
                break;
            case 0:
                this.xing3.active = false;
                this.xing2.active = false;
                this.xing1.active = false;
                break;
            default:
                break;
        }
        if (bar >= 1) {
            bar = 1;
        }
        this.logo.x = bar * 700;
    },
    //
    MinusStep() {
        this.stepnum -= 1;
        if (this.stepnum <= 0) {
            this.stepnum = 0;
            //this.EndOver();
        }
        this.stepLabel.string = this.stepnum;
    },
    //5
    AddFiveStep() {
        this.stepnum += 5;
        this.stepLabel.string = this.stepnum;
    },
    AddTwoStep() {
        this.stepnum += 2;
        this.stepLabel.string = this.stepnum;
    },
    //
    GetStarNum() {
        // if(this._score>(lvdata["Lv"+comeInfo.now_lv].one_star*3)){
        //     return 3;
        // }else if(this._score>(lvdata["Lv"+comeInfo.now_lv].one_star*2)){
        //     return 2;
        // }else if(this._score>(lvdata["Lv"+comeInfo.now_lv].one_star)){
        //     return 1;
        // }else {
        //     return 0;
        // }
        if (this._score > (lvdata.one_star * 3)) {
            return 3;
        } else if (this._score > (lvdata.one_star * 2)) {
            return 2;
        } else if (this._score > (lvdata.one_star)) {
            return 1;
        } else {
            return 0;
        }
    },
    //
    EndOver() {
        let e = cc.instantiate(this.end);
        this.node.addChild(e);
        e.setPosition(0, 0);
        e.getComponent("End").InitView();
    }
});
